import {
	mapState,
	mapMutations
} from 'vuex';
import store from "../../../../store/index.js";
import api from "../../../../api/api.js";
export default {
	computed: mapState(['hasLogin', 'userInfo', 'token']),
	data() {
		return {
			array: ['在线', '隐身'],
			index: 0
		}
	},
	methods: {
		...mapMutations(['logout']),
		logoOut() {
			if (this.hasLogin) {
				uni.showToast({
					icon: 'none',
					title: '注销成功',
				});
				this.logout();
				uni.reLaunch({
					url: '../../../../pages/my/my'
				});
			} else {
				uni.showToast({
					icon: 'none',
					title: '您尚未登录哟~',
				});
			}
		},
		gotoDevelop() {
			uni.navigateTo({
				url: 'about/about'
			})
		},
		switch1Change: function(e) {
			console.log('switch1 发生 change 事件，携带值为', e.target.value)
		},
		bindPickerChange: function(e) {
			console.log('picker发送选择改变，携带值为：' + e.target.value)
			this.index = e.target.value
		}
	}
}
